<template>
  <div class="product-detail-img">
    <div>
      <common-header class="head" :options="headerOptions" style="margin-bottom: 100px"></common-header>
      <div class="title_content"><span class="title"> {{title}}</span></div>

        <see-box class="imgbox">
          <swiper :options="swiperOption" class="swiper-box-full">
          <swiper-slide v-for="image in imgList" :key="image.path">
            <see-item :img="image"></see-item>
          </swiper-slide>
            <div class="swiper-pagination" slot="pagination"></div>
            <div class="swiper-button-prev my " slot="button-prev"></div>
            <div class="swiper-button-next" slot="button-next"></div>
          </swiper>

        </see-box>
    </div>
    <operate-buttons :resourceId="resourceId"></operate-buttons>

  </div>

</template>

<script>
  require('swiper/dist/css/swiper.css')
  import {swiper, swiperSlide} from 'vue-awesome-swiper'
  import CommonHeader from '../common/CommonHeader.vue'
  import OperateButtons from './OperateButtons.vue'
  import VueSee from 'vue-see'
  import Vue from 'vue'
  var options = {
    tapToClose: true,
    shareEl: false,
  }
  Vue.use(VueSee, options)
  export default {
    name: 'app',
    components: {swiperSlide, swiper, CommonHeader, OperateButtons},
    data() {
      return {
        swiperOption: {
          pagination: '.swiper-pagination',
          paginationClickable: true,
          nextButton: '.swiper-button-next',
          prevButton: '.swiper-button-prev',
          spaceBetween: 30
        },
        fullSwiperOption: {
          pagination: '.swiper-pagination',
          paginationClickable: true,
          spaceBetween: 30
        },
        headerOptions: {title: '图片详情', align: 'center'},
        resourceId: this.$route.params.resourceID,
        images: '',
        author: '',
        title: '',
        imgList: []

      }
    },

    mounted: function () {
      this.resourceId = this.$route.params.resourceID

      this.loadResDetailData()

      console.log(this.imgList)
    },

    methods: {

      handleImgs: function (images) {
        for (var i = 0; i < images.length; i++) {
          this.imgList.push({
            u: images[i].path,
            w: 500,
            h: 365,
            c: images[i].describe
          })
        }
        console.log(this.imgList)
      },

      loadResDetailData: function () {
        let that = this
        var url = this.$global.getUrlByAppId("PORTAL") + "/loadResourceDetailData?userId=" + this.$global.userId
        let para = {}
        para.resourceId = this.resourceId
        this.$http.post(url, qs.stringify({para: JSON.stringify(para)}))
          .then(function (response) {
//            console.log(response);
            that.title = response.data.resource.name
            that.images = response.data.resource.NewResourceFiles
            that.handleImgs(that.images)
          })
          .catch(function (response) {
            console.log(response)
          })
      }


    }

  }
</script>

<style lang="scss">
  .product-detail-img {

    .head{
      margin-bottom: 20% !important;
    }
    .swiper-box {
      width: 100%;
      height: 50%;
      margin: 0 auto;
    }
    .swiper-item {
      text-align: center;
      font-size: 18px;
      background: #fff;
      /* Center slide text vertically */
      /*display: -webkit-box;
      display: -ms-flexbox;
      display: -webkit-flex;
      display: flex;*/
      -webkit-box-pack: center;
      -ms-flex-pack: center;
      -webkit-justify-content: center;
      justify-content: center;
      -webkit-box-align: center;
      -ms-flex-align: center;
      -webkit-align-items: center;
      align-items: center;
    }
    .swiper-slide {
      img {
        display: block;
        margin: auto;
        height: 300px;
        width: 96%;
      }
    }
    .full {
      width: 100%;
    }


    .imgbox{
      height: auto;
      width: auto;
    }
    figcaption{
      display: none;
    }

    .pswp__caption__center {
      font-size: 20px;
      margin-bottom: 5%;
      word-wrap: break-word;
      word-break: normal;
      display: inline-block;

    }

    .title_content {
      padding-bottom: 20px;
      padding-left: 0.5em;
      padding-right: 1.5em;
      --width: 100vw;
      box-sizing: border-box;
    }
    .title {
      height: 32px;
      width: 90vw;
      font-size: 26px;
      word-wrap: break-word;
      word-break: normal;
      display: inline-block;
      line-height: 26px;
    }

    .footer {
      position: fixed;
      width: 99%;
      height: 10%;
      bottom: 0px;
      box-sizing: border-box;
    }

    .swiper-pagination{
      height: 30vw;
    }
  }
</style>



