<template>
  <div class="new-text">
    <common-header :options="options"></common-header>
    <div style="margin-top: 90px">
      <head-line ref="headline"></head-line>
      <span class="tip">已输入{{ length }}个字</span>
      <editor @change="updateData" :content="content" :height="500" :z-index="1000" :auto-height="true"
              :show-module-name="false"></editor>
    </div>

    <foot-button v-if="!editable" v-bind:save="textSave" v-bind:commit="textCommit" class="btndiv"></foot-button>
    <div class="bottom-button">
      <mt-button v-if="editable" class="footBtn" @click="editSave" type="danger" size="small">保存修改</mt-button>
    </div>

  </div>

</template>

<script>
  import CommonHeader from '@/components/common/CommonHeader'
  import FootButton from '@/components/create/Button'
  import HeadLine from '@/components/create/HeadLine'
  import VueHtml5Editor from 'vue-html5-editor'
  import editorOptions from './../../assets/js/editorOptions'
  import {Toast} from 'mint-ui'

  let reId = '';
  let generateResId = function (len, radix) {
    let CHARS = '0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz'.split('');
    let chars = CHARS, uuid = [], i;
    radix = radix || chars.length;

    if (len) {
      // Compact form
      for (i = 0; i < len; i++) uuid[i] = chars[0 | Math.random() * radix];
    } else {
      // rfc4122, version 4 form
      let r;

      // rfc4122 requires these characters
      uuid[8] = uuid[13] = uuid[18] = uuid[23] = '-';
      uuid[14] = '4';

      // Fill in random data.  At i==19 set the high bits of clock sequence as
      // per rfc4122, sec. 4.1.5
      for (i = 0; i < 36; i++) {
        if (!uuid[i]) {
          r = 0 | Math.random() * 16;
          uuid[i] = chars[(i == 19) ? (r & 0x3) | 0x8 : r];
        }
      }
    }
    reId = uuid.join('');
    return reId;
  }
  const editor = new VueHtml5Editor(editorOptions(generateResId()).options);

  export default {
    components: {CommonHeader, FootButton, HeadLine, editor, Toast},
    data() {
      return {
        options: {
          title: '新建文本',
          align: 'center'
        },
        editorOptions: {},
        resourceId: '',
        fulltext: '',
        content: '',
        length: 0,
        username: '',
        editable: false
      }
    },

    mounted: function () {
      this.resourceId = reId;
      this.findUserByID(this.$global.userId)
      if (this.$route.params.resourceID) {
        this.resourceId = this.$route.params.resourceID
        this.editable = true
        this.options.title = '修改文本'
        this.loadResource(this.resourceId)
        this.$refs.headline.subheadVisible = true
        this.$refs.headline.headVisible = true
      }
      console.log(this.editable)
    },

    watch: {
        content: "updateData"
    },

    methods: {
      loadResource(resourceId) {
        let that = this
        var url = this.$global.getUrlByAppId("PORTAL") + "/loadDetailInfo/" + resourceId;
        this.$http.get(url).then(function (response) {
          console.log(response.data)
          that.$refs.headline.head = response.data.yinti
          that.$refs.headline.headline = response.data.biaoti;
          that.$refs.headline.subhead = response.data.fubiaoti;
          that.content = response.data.html_content;

        }).catch(function (error) {
          console.log(error.response.data);
        })
      },

      editSave() {
        this.newResource(false)
      },


      updateData: function (data) {
        this.content = data
        this.length = data.replace(/<[^>]+>/g, "").replace(/&nbsp;/g, "").length
      },
      textSave: function () {
        this.newResource(false);
      },
      textCommit: function () {
        this.newResource(true); //提交并且提交审核
      },
      newResource: function (auflg) {
        var that = this;
        //本地文件数量
        var params = {
          attachmentList: [],
          auditorName: '',
          authorName: this.username,
          content: '<p>' + this.content + '<br/></p>',
          contentText: this.content.replace(/<[^>]+>/g, ""),
          editorId: this.$global.userId,
          eyebrowHead: this.$refs.headline.head,
          folderId: 'PRODUCT_ROOT_FOLDERID',
          keyWords: '',
          productStatus: '草稿',
          source: '',
          subtitle: this.$refs.headline.subhead,
          title: this.$refs.headline.headline,
          topicName: ''
        }
        if (!this.$refs.headline.headline) {
          Toast("标题不能为空")
          return
        }
        if (!this.content) {
          Toast("内容不能为空")
          return
        }
        console.log(params)
        this.$http.put(this.$global.getUrlByAppId("CRE") + '/api/resources/richText/' + this.resourceId + '?newVersion=fasle', JSON.stringify(params))
          .then(function (response) {
            console.log(response)
            if (response.status === 200) {
              that.onCreateSuccess(true, auflg, that.resourceId)
              Toast("保存成功")
            }

          })
          .catch(function (error) {
            that.onCreateSuccess(false);
          })
      },
      onCreateSuccess: function (flag, auflg, resourceId) {
        if (flag) {
          if (auflg) {//提交审核按钮
            router.push('/audit4draft/' + resourceId);
          } else {
           // router.push('/product');
            router.push({name : 'product',params : {resourceId : this.resourceId,operator : 'refresh'}});
          }
        } else {
          Toast('创建失败');
        }
      },
      findUserByID: function (userId) {
        let that = this
        this.$http.get(this.$global.getUrlByAppId("CRE") + '/api/authorization/findUsersById?userIds=e18a0f771ac4485fa9107d8382326e57')
          .then(function (res) {
            if (res.status === 200) {
              that.username = res.data.userList[0].name
            }
          })
          .catch(function () {
            that.username = ''
          })
      }
    },


  }
</script>

<style lang="scss">

  .new-text {
    .tip {
      padding-left: 70%;
      text-align: right;
      font-size: xx-small;
    }
    img {
      width: 90%;
      height: 100%;
    }

    .footBtn{
      margin-left: 40%;
      position: fixed;
      bottom:3px;
    }
    .bottom-button{
      width: 100%;
      position: fixed;
      background-color: #fff;
      height: 40px;
      bottom: 0px;
      border-top:1px solid #dedede;
    }
  }


</style>



