<template>
  <div class="product-detail-text">
    <common-header :options="headerOptions"></common-header>
    <div class="tab-detail">
      <div class="title_content"><span class="title"> {{title}}</span></div>
      <hr>
      <div class="author_content">作者: <span class="author">{{author}}</span></div>
      <div class="content" v-html="content"></div>
    </div>
    <operate-buttons :resourceId="resourceId"></operate-buttons>
  </div>
</template>
<script>
  import CommonHeader from '../common/CommonHeader.vue'
  import MtField from "../../../node_modules/mint-ui/packages/field/src/field";
  import OperateButtons from './OperateButtons.vue'
  export default {
    components: {MtField, CommonHeader, OperateButtons},
    data () {
      return {
        resourceId: this.$route.params.resourceID,
        title: '',
        content: '',
        author: '',
        headerOptions: {title: '文本详情', align: 'center'}
      }
    },
    mounted: function () {
      //this.resourceId = this.$route.params.resourceID;
      this.loadDetailInfo();
    },
    methods: {
      loadDetailInfo: function () {
        let that = this
        var url = this.$global.getUrlByAppId("PORTAL") + "/loadDetailInfo/" + this.resourceId;
        this.$http.get(url).then(function (response) {
          that.title = response.data.biaoti;
          that.author = response.data.author;
          that.content = response.data.html_content;
        }).catch(function (error) {
          console.log(error.response.data);
        })
      }
    }
  }
</script>
<style lang="scss">
  .product-detail-text {
    font-size: 16px;
    height: 100%;
    .tab-detail{
      img {
        width: 90%;
        height: 100%;
      }
    }
    .title_content {
      margin-top: 15%;
      padding-bottom: 20px;
      padding-left: 1.5em;
      padding-right: 1.5em;
      --width: 100vw;
      box-sizing: border-box;
    }
    .title {
      height: 32px;
      width: 90vw;
      font-size: 26px;
      word-wrap: break-word;
      word-break: normal;
      display: inline-block;
      line-height: 26px;
    }
    .author_content {
      text-indent: 2em;
      font-size: 10px !important;
      margin: 10px 0px 10px 0px;
    }
    .author {
      font-size: 9px !important;
      text-indent: 1em;
    }
    .content {
      margin-left: 1.5em;
      margin-right: 1.5em;
    }
    // .footer {
    //   position: fixed;
    //   width: 100%;
    //   height: 56px;
    //   border-top:1px solid #dedede;
    //   bottom: 0px;
    //   box-sizing: border-box;
    // }
  }
</style>



