import Vue from 'vue'
import Router from 'vue-router'
import Product from '../components/product/Product.vue'
import NewText from '../components/create/NewText.vue'
import NewImg from '../components/create/NewImg.vue'
import NewVideo from '../components/create/NewVideo.vue'
import ProductSearch from '../components/product/ProductSearch.vue'
import cgAudit from '../components/product/buttonOperation/cgAudit.vue'
import productMove from '../components/product/buttonOperation/productMove.vue'
import issuer from '../components/product/buttonOperation/issuer.vue'
import textDetail from '../components/detail/TextDetail.vue'
import imageDetail from '../components/detail/ImageDetail.vue'
import videoDetail from '../components/detail/VideoDetail.vue'


Vue.use(Router);
const router = new Router({
  mode: 'history',
  history: true, // use history=false when testing
  saveScrollPosition: true,
  routes: [{
    path: '*',
    redirect: '/product'
  },
    {
      path: '/newvideo',
      name: 'NewVideo',
      component: NewVideo
    },
    {
      path: '/newimg',
      name: 'NewImg',
      component: NewImg
    },
    {
      path: '/newtext',
      name: 'NewText',
      component: NewText
    }, {
      path: '/product',
      name: 'product',
      component: Product,
      meta: {keepAlive: true}
    }, {
      path: '/search',
      name: 'ProductSearch',
      component: ProductSearch,
      meta: {keepAlive: true}
    }, {
      path: '/audit4draft/:resourceID',
      name: 'audit4draft',
      component: cgAudit
    }, {
      path: '/move/:resourceID/:organizationId/:from',
      name: 'move',
      component: productMove
    }, {
      path: '/issuer/:resourceID/:from',
      name: 'issuer',
      component: issuer
    }, {
      path: '/textDetail/:resourceID',
      name: 'textDetail',
      component: textDetail
    }, {
      path: '/imageDetail/:resourceID',
      name: 'imageDetail',
      component: imageDetail,
      meta:{keepAlive:true}
    }, {
      path: '/videoDetail/:resourceID',
      name: 'videoDetail',
      component: videoDetail
    }, {
      path: '/edittext/:resourceID',
      name: 'EditText',
      component: NewText
    }
  ]
});

router.beforeEach((to, from, next) => {
  if (document.getElementById('productList'))
    from.meta.scrollTop = document.getElementById('productList').scrollTop;
  if (document.getElementById('searchResults'))
    from.meta.scrollTop = document.getElementById('searchResults').scrollTop;

  next();
})

window.router = router;

export default router



